___CSS_LOADER_EXPORT___.push([module.id, `#weatherIcon {
    height: 75px;
    width: auto;
    margin-top: 0.5rem;
}

body {
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    margin: 0;
    background-color: #eef2f7;
}

#container {
    background-color: white;
    padding: 2rem;
    border-radius: 8px;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.15);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    max-width: 400px;
}

#inputArea {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    width: 100%;
}

#locationInput {
    padding: 0.5rem;
    border: 1px solid lightgray;
    border-radius: 4px;
    width: 100%;
}

button {
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 4px;
    background-color: lightseagreen;
    color: white;
    cursor: pointer;
}

button:hover {
    background-color: rgb(35, 224, 214);
}

#dataArea {
    text-align: center;
}

.weatherText {
    margin: 0.25rem 0;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,YAAY;IACZ,WAAW;IACX,kBAAkB;AACtB;;AAEA;IACI,yCAAyC;IACzC,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,iBAAiB;IACjB,SAAS;IACT,yBAAyB;AAC7B;;AAEA;IACI,uBAAuB;IACvB,aAAa;IACb,kBAAkB;IAClB,yCAAyC;IACzC,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,SAAS;IACT,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,WAAW;IACX,WAAW;AACf;;AAEA;IACI,eAAe;IACf,2BAA2B;IAC3B,kBAAkB;IAClB,WAAW;AACf;;AAEA;IACI,oBAAoB;IACpB,YAAY;IACZ,kBAAkB;IAClB,+BAA+B;IAC/B,YAAY;IACZ,eAAe;AACnB;;AAEA;IACI,mCAAmC;AACvC;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,iBAAiB;AACrB","sourcesContent":["#weatherIcon {\n    height: 75px;\n    width: auto;\n    margin-top: 0.5rem;\n}\n\nbody {\n    font-family: Arial, Helvetica, sans-serif;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    min-height: 100vh;\n    margin: 0;\n    background-color: #eef2f7;\n}\n\n#container {\n    background-color: white;\n    padding: 2rem;\n    border-radius: 8px;\n    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.15);\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    gap: 1rem;\n    max-width: 400px;\n}\n\n#inputArea {\n    display: flex;\n    flex-direction: column;\n    gap: 0.5rem;\n    width: 100%;\n}\n\n#locationInput {\n    padding: 0.5rem;\n    border: 1px solid lightgray;\n    border-radius: 4px;\n    width: 100%;\n}\n\nbutton {\n    padding: 0.5rem 1rem;\n    border: none;\n    border-radius: 4px;\n    background-color: lightseagreen;\n    color: white;\n    cursor: pointer;\n}\n\nbutton:hover {\n    background-color: rgb(35, 224, 214);\n}\n\n#dataArea {\n    text-align: center;\n}\n\n.weatherText {\n    margin: 0.25rem 0;\n}"],"sourceRoot":""}]);
